import 'rxjs/add/operator/take';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/switchMap';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/let';
import { Injectable } from '@angular/core';
import { Store } from '@ngrx/store';
import { Router, CanActivate, ActivatedRouteSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';

import { GoogleBooksService } from '../services/google-books';
import * as fromRoot from '../reducers';
import * as project from '../actions/project';


/**
 * Guards are hooks into the route resolution process, providing an opportunity
 * to inform the router's navigation process whether the route should continue
 * to activate this route. Guards must return an observable of true or false.
 */
@Injectable()
export class ProjectExistsGuard implements CanActivate {
  constructor(
    private store: Store<fromRoot.State>,
    private googleProjects: GoogleBooksService,
    private router: Router
  ) { }

  /**
   * This method creates an observable that waits for the `loaded` property
   * of the collection state to turn `true`, emitting one time once loading
   * has finished.
   */
  waitForCollectionToLoad(): Observable<boolean> {
    return this.store.select(fromRoot.getCollectionLoaded)
      .filter(loaded => loaded)
      .take(1);
  }

  /**
   * This method checks if a project with the given ID is already registered
   * in the Store
   */
  hasProjectInStore(id: string): Observable<boolean> {
    return this.store.select(fromRoot.getProjectEntities)
      .map(entities => !!entities[id])
      .take(1);
  }

  /**
   * This method loads a project with the given ID from the API and caches
   * it in the store, returning `true` or `false` if it was found.
   */
  hasProjectInApi(id: string): Observable<boolean> {
    return this.googleProjects.retrieveProject(id)
      .map(projectEntity => new project.LoadAction(projectEntity))
      .do((action: project.LoadAction) => this.store.dispatch(action))
      .map(project => !!project)
      .catch(() => {
        this.router.navigate(['/404']);
        return of(false);
      });
  }

  /**
   * `hasProject` composes `hasProjectInStore` and `hasProjectInApi`. It first checks
   * if the project is in store, and if not it then checks if it is in the
   * API.
   */
  hasProject(id: string): Observable<boolean> {
    return this.hasProjectInStore(id)
      .switchMap(inStore => {
        if (inStore) {
          return of(inStore);
        }

        return this.hasProjectInApi(id);
      });
  }

  /**
   * This is the actual method the router will call when our guard is run.
   *
   * Our guard waits for the collection to load, then it checks if we need
   * to request a project from the API or if we already have it in our cache.
   * If it finds it in the cache or in the API, it returns an Observable
   * of `true` and the route is rendered successfully.
   *
   * If it was unable to find it in our cache or in the API, this guard
   * will return an Observable of `false`, causing the router to move
   * on to the next candidate route. In this case, it will move on
   * to the 404 page.
   */
  canActivate(route: ActivatedRouteSnapshot): Observable<boolean> {
    return this.waitForCollectionToLoad()
      .switchMap(() => this.hasProject(route.params['id']));
  }
}



